const getColors = (color) => {
  switch (color) {
    case 0:
      return "#F8DF6C";
    case 1:
      return "#FFB5E9";
    case 2:
      return "#ffb394";
    case 3:
      return "#A9E295";
    case 4:
      return "#C5D6F5";
    default:
      return "#C5C5C5";
  }
};

const getEmotion = (type) => {
  switch (type) {
    case 0:
      return "";
    case 1:
      return "";
    case 2:
      return "";
    case 3:
      return "";
    case 4:
      return "";
    default:
      return "";
  }
};

const getCover = (type) => {
  switch (type) {
    case 0:
      return happyCard;
    case 1:
      return surprisedCard;
    case 2:
      return angryCard;
    case 3:
      return anxietyCard;
    case 4:
      return sadCard;
    default:
      return neutralCard;
  }
};

export { getColors, getEmotion, getCover };
